<template>
  <div class="about">
    <v-card
      class="mx-auto"
      max-width="344"
    >
      <v-card-title class="headline">About</v-card-title>
      <v-card-text>
        <div>User Name</div>
        <strong>
          {{userName}}
        </strong>
        <div>Is mobile</div>
        <strong>
          {{isMobile}}
        </strong>
        <div>Time of build</div>
        <strong>
          {{timestamp}}
        </strong>
        <div>Version</div>
        <strong>
          {{appVersion}}
        </strong>
        <div>gitVersion</div>
        <strong>
          {{gitVersion}}
        </strong>
        <div>Mode</div>
        <strong>
          {{mode}}
        </strong>
      </v-card-text>
      <v-card-actions>
        <v-btn @click="msgBtn">MSG</v-btn>
        <v-btn @click="errBtn">ERR</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script lang="js">
  import {Component, Vue} from 'vue-property-decorator';
  import {UserModule} from '../store/modules/user'
  import {AppModule} from '../store/modules/app'
  import {AlertsModule} from '../store/modules/alerts';

  @Component
  export default class UserForm extends Vue {

    msgBtn(){
      AlertsModule.setMessage('msg')
    }
    errBtn(){
      AlertsModule.setError('msg')
    }
    get userName() {
      return UserModule.getUsername;
    }
    get isMobile() {
      return AppModule.isMobile;
    }
    get timestamp() {
      return AppModule.timeOfBuild
      // return  document.documentElement.dataset.buildTimestampUtc;
    }
    get appVersion() {
      return AppModule.appVersion;
    }
    get gitVersion(){
      return GIT_DESCRIBE.raw
    }
    get mode(){
      return process.env.NODE_ENV
    }
  }
</script>
